  .hover-tooltip-container .hover-tooltip {
    opacity: 0;
    visibility: hidden;
    position: absolute;
    /*rtl:ignore*/
    left: 50%;
    top: calc(-50% - 5px);
    transform: translate(-50%, -50%);
    background-color: #fff;
    border: 1px solid rgba(0, 0, 0, 0.2);
    border-radius: .3rem;
