}

img.userpicture {
    margin-right: 0.5rem;
}

img.resize {
    height: 1em;
    width: 1em;
}

.action-menu .dropdown-toggle {
    text-decoration: none;
}

.action-menu {
    white-space: nowrap;
}

.block img.resize {
